import { Component, OnInit } from '@angular/core';
import {Location} from "@angular/common";
import {EventProviderService} from "../../services/event-provider.service";
import {MenuController} from "@ionic/angular";
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-notifications',
  templateUrl: './notifications.page.html',
  styleUrls: ['./notifications.page.scss'],
})
export class NotificationsPage implements OnInit {

  ch_id: any;
  constructor(
      private location: Location,
      private event_provider: EventProviderService,
      private menu: MenuController,
      private translate : TranslateService
  ) { }

  ngOnInit() {
    this.event_provider.getChurchData.subscribe(value => {
      if(!(Object.keys(value).length === 0)){
        let church = value.church;
        if (church){
          this.ch_id = church.ch_id
        }
      }
    });
  }

  ionViewDidEnter(){
    this.event_provider.hidefooter(true, 'other');
  }

  gotobackPage() {
    if(this.ch_id === '1000'){
      this.menu.toggle();
    } else {
      this.location.back();
    }
  }
}
